  width: 320px;
  height: 320px;
  color: #333;
  background-color: rgba(255, 255, 255, 0.9);
  border: 1px solid rgba(0, 0, 0, 0.6);
  border-radius: 6px;
  box-shadow: 0 2px 10px 2px rgba(0, 0, 0, 0.3);
  outline: none;
}

.photoviewer-modal:focus-visible {
  outline: thick solid rgba(0, 0, 0, 0.2);
}

