  display: inline-block;
}

.pagination a {
  color: black;
  float: left;
  border-radius: 5px;
  padding: 8px 16px;
  text-decoration: none;
  cursor: pointer;
}

.pagination a.active {
  transition: all 0.3s;
  background-color: #2f6dd5;
  border-radius: 5px;
  color: white;
}

.pagination a:hover:not(.active) {
  transition: all 0.5s;
  background-color: #ddd;
